'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property' ;


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name Function
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/function/ctor}
*/
import Function from './../ctor';
setReadOnly( ns, 'Function', Function );

/**
* @name function2string
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/function/to-string}
*/
import function2string from './../to-string';
setReadOnly( ns, 'function2string', function2string );


// EXPORTS //

export default ns;
